import React, { FC } from "react";

import { StyledRouteContent, StyledRouteContentProps } from "./RouteContent.styles";

export interface RouteContentProps extends StyledRouteContentProps {
	dataQA: string;
	children?: React.ReactNode;
	className?: string;
}

const RouteContent: FC<RouteContentProps> = ({
	children,
	dataQA,
	className,
	hasLocalNav,
	contentSize = "withoutLocalNav",
	zIndex }) => (
	<StyledRouteContent
		as={contentSize === "fullScreen" ? "div" : "main"}
		data-qa={dataQA}
		className={className}
		hasLocalNav={hasLocalNav}
		contentSize={contentSize}
		zIndex={zIndex}>
		{children}
	</StyledRouteContent>
);

RouteContent.displayName = "RouteContent";
export default RouteContent;
